.contact-form {
  position: absolute;
  width: 100%;
  height: 100%;
  max-width: 600px;
  max-height: 500px;
  display: flex;
  flex-direction: column;
  padding-left: 5%;
}

.name,
.email,
.message {
  background-color: #1f2833;
  border: none;
  font-family: "Open Sans", sans-serif;
  color: white;
  padding-left: 16px;
  padding-right: 16px;
  margin-right: 1px;
  margin-left: 1px;
  margin-bottom: 1em;
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  opacity: 1; /* Firefox */
}

.name,
.email {
  min-height: 56px;
}

.name {
  border-radius: 8px 8px 0 0;
}

.email {
  border-radius: 0;
}

.message {
  height: 100%;
  padding-top: 1em;
  resize: none;
  border-radius: 0 0 8px 8px;
  line-height: 28px;
}

.buttonInline {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.buttonInline input:disabled {
  background-color: #45a29e;
  cursor: default;
}

textarea:focus,
input:focus,
input[type]:focus,
.uneditable-input:focus {
  -webkit-appearance: none;
  box-shadow: 0 0 1pt 1pt #45a29e;
  -webkit-box-shadow: 0 0 1pt 1pt #45a29e;
  -moz-box-shadow: 0 0 1pt 1pt #45a29e;
}

p.active {
  margin-right: 24px;
  display: block;
  letter-spacing: 1.1px;
  color: white;
}

p.inactive {
  display: none;
}

span.email-text {
  font-size: 1.2rem;
  font-weight: 400;
  color: #66fcf1;
  text-decoration: none;
  transition: color 0.3s;
  width: 300px;
  line-height: 1.8em;
}

.email-text:hover {
  color: #45a29e;
  cursor: pointer;
}

#tooltip {
  color: white;
  background-color: #45a29e;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 6px 12px;
}

.rc-tooltip-arrow {
  border-bottom-color: #45a29e !important;
}

.peace-emoji {
  font-size: 24px;
}

@media only screen and (max-width: 1000px) {
  .contact-form {
    margin-bottom: 64px;
    height: 450px;
    position: static;
    padding: 0;
  }
}
