.nav-list {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
}

.nav-list li {
  list-style: none;
  font-size: 1rem;
  font-weight: 400;
  text-align: center;
  letter-spacing: 1px;
}

.site-logo {
  font-size: 3rem;
  font-weight: 700;
  cursor: pointer;
  color: #66fcf1;
}

.link:not(:last-of-type) {
  margin-right: 4em;
}

.link a {
  color: white;
  text-decoration: none;
}

.link a:hover {
  cursor: pointer;
  color: white;
}

.link a:after {
  content: '';
  position: relative;
  width: 0;
  height: 2px;
  display: block;
  margin-top: 5px;
  right: 0;
  background: #66fcf1;
  transition: width 0.5s ease;
  -webkit-transition: width 0.5s ease;
  border-radius: 600px;
}

.link a:hover:after {
  width: 100%;
  background: #66fcf1;
}

.logo {
  position: relative;
  margin-right: auto;
  width: 48px;
}

.logo-container {
  display: flex;
}

.logoWhite {
  position: relative;
  z-index: 1;
  cursor: pointer;
}

.logoGreen {
  position: absolute;
  top: 0px;
  z-index: 0;
  left: 0px;
  transition: all 0.5s;
}

.logoWhite:hover ~ .logoGreen {
  top: 6px;
  left: 10px;
}

@media only screen and (max-width: 800px) {
  .nav-list {
    height: 64px;
  }

  .full-nav {
    height: 64px;
  }

  .logoGreen {
    display: none;
  }

  .link {
    display: none;
  }
}
